import React, { useState } from "react";
import Destop from "./views/Destop";
import { Mobile } from "./views/Mobile";
const Nav = () => {

  const [click, setClick] = useState(false)
  
  const handleClick = () => {
    setClick(!click)
  }
  return (
    <>
    <Destop/>
      <Mobile />
      </>
  );
};

export default Nav;
